#main {
  text-align: center;
  margin-top: 10%;
}
@media only screen and (max-width:400px) {
  #main {
    text-align: center;
    margin-top: 70%;
  }
}
button{
  background-color: white;
  border: solid 1px grey;
  margin: 2px;
  width: 30px;
  height: 30px;
}
button:hover{
  box-shadow: 5px 5px 7px;
  background-color: rgb(243, 191, 191);
}
#first-row,#second-row,#third-row{
  display:flex;
  justify-content: center;
}
#reset {
  width: auto;
  height: auto;
  margin-top: 20px;
  border-radius: 10px;
  padding: 10px 20px;
}
#reset:hover {
  width: auto;
  height: auto;
  margin-top: 20px;
  border-radius: 10px;
  padding: 10px 20px;
  background-color: grey;
}
